import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import {Button, CardActionArea, CardActions } from '@mui/material';
import AudioImage from "../Audio.png";

const CardData=({mediaName,src,description})=>{
if(mediaName=="Image"){
  return (
    <Card>
      <CardActionArea >
        <img src={src} style={{padding:"5px",margin:"5px",width:"90%"}}/>
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
         {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined" style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}} >More Images</Button>
      </CardActions>
    </Card>
  )}

if(mediaName=="Vedio"){
  return (
    <Card >
      <CardActionArea>
          
      <video  width="80%" height="80%" style={{padding:"5px",margin:"5px",width:"90%"}} controls controlsList="nodownload">
  <source src={src} type="video/mp4"/>
  <source src={src} type="video/ogg" />
  Your browser does not support the video tag.
</video>    
                    
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
          {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE Videos</Button>
      </CardActions>
    </Card>
  )}
if(mediaName=="Audio"){
  return (
    <Card>
      <CardActionArea>
  <img src={AudioImage} style={{padding:"5px",margin:"5px",width:"90%"}}></img>

<audio style={{padding:"5px",margin:"10px",width:"80%"}} controls controlsList="nodownload">
  <source src={src} type="audio/ogg" />
  <source src={src} type="audio/mpeg"/>
  Your browser does not support the audio tag.
</audio>
                                           
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
        {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE AUDIOS</Button>
      </CardActions>
    </Card>
  )}
  }

  export default CardData;
